<template>
<div class="shopcart">
    <div class="not-box" v-if="shopcartData.length == 0">
		<van-nav-bar
			title="饮品与小食"
			@click-right="onClickRight"
		/>
        <img class="img-layout not-have" :src="require('../../assets/images/noyemian.png')" alt="购物车为空" />
        <button class="btn" @click="showMenuHandle">去喝一杯 ~</button>
    </div>
    <div class="list" v-if="shopcartData.length > 0">
		<van-nav-bar
			title="饮品与小食"
			@click-right="onClickRight"
			:right-text="isEdit ? '完成' : '编辑'"
		/>
		<ul>
			<li class="item border-bottom-1px" v-for="(item,index) in shopcartData" :key="item.sid">
				<van-swipe-cell :disabled="isDisabled">
					<van-cell :border="false" >
						<div class="goods-info">
							<div class="goods-check">
								<van-checkbox @click="singleChoice" v-model="item.isCheck" icon-size="18px" />
							</div>
							<div class="goods-img">
								<img class="img-layout" :src="item.small_img" alt="">
							</div>
							<div class="goods-name">
								<h3 class="name">{{item.name}}</h3>
								<p class="type">{{item.rule}}</p>
								<p class="price">￥{{item.price}}</p>
							</div>
							<div class="goods-btn">
								<van-stepper @change="modifyShopcartCount(item)" theme="round" v-model="item.count" button-size="18" min="1" max="99" disable-input />
							</div>
						</div>
					</van-cell>
					<template #right>
						<van-button @click="removeOneShopcart(item,index)" square type="danger" text="删除" />
					</template>
				</van-swipe-cell>
			</li>
		</ul>
    </div>
	<div class="total" v-if="getCount > 0">
		<van-submit-bar
			class="buy-btn"
			v-show="!isEdit"
			:price="total" 
			text-align="right" 
			button-text="去结算"
			button-color="#d8b58a"
			@submit="pay"
			>
			<van-checkbox v-model="allChecked" @click="selectAll">全选</van-checkbox>
		</van-submit-bar>
		<van-submit-bar
			class="buy-btn delete"
			v-show="isEdit"
			button-text="删除选择"
			button-color="#d8b58a"
			@click="selectAll"
			@submit="removeSomeShopcart"
			>
			<van-checkbox v-model="allChecked" @click="selectAll">全选</van-checkbox>
		</van-submit-bar>
	</div>
</div>
</template>

<script>
import '../../assets/scss/shopcart.scss'
import {
	mapGetters
} from 'vuex'
import { utils } from '../../assets/js/utils'

export default {
    data() {
        return {
			shopcartData: [],
			allChecked: false,
			isDisabled: true,
			isEdit: false,
			total: 0
        }
    },
    created() {
        this.getShopCartData();
    },
    methods: {
		// 购买商品
		pay() {
			// 购买商品的sid
			let sids = [];	

			for(let i = 0; i < this.shopcartData.length; i++) {
				if(this.shopcartData[i].isCheck) {
					sids.push(this.shopcartData[i].sid)
				}
			}

			if(sids.length === 0) {
				this.$toast("您还未选择任何商品");
				return ;
			}

			console.log(sids);

			this.$router.push({
				name: 'Pay',
				query: {
					sids
				}
			})
			
		},
		// 计算价格
		calc() {
			let total = 0;
			this.shopcartData.forEach(item => {
				if(item.isCheck) {
					total += item.count * item.price
				}	
			})

			this.total = total * 100;
		},
		// 删除多件商品
		removeSomeShopcart() {
			// 判断是否登录过
            if (!this.getToken) {
                return;
			}

			let sids = [];

			this.shopcartData.forEach(item => {
				if(item.isCheck) {
					sids.push(item.sid)
				}
			})

			console.log(sids);

			let data = utils.queryString({
                appkey: this.appkey,
				tokenString: this.getToken,
				sids: JSON.stringify(sids)
			})

			let params = {
				appkey: this.appkey,
				tokenString: this.getToken
			}

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'POST',
				url: '/deleteShopcart',
				data
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				if(res.data.code === 7000) {					
					for(let i = this.shopcartData.length -1 ; i >= 0; i--) {
						if(this.shopcartData[i].isCheck) {
							this.shopcartData.splice(i,1);
						}
					}
					this.singleChoice();
					this.calc();
					this.$store.dispatch('getShopcartCount',params)
				}
				this.$toast(res.data.msg)
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})
		},
		// 删除单个购物车商品
		removeOneShopcart(item,index) {
			// 判断是否登录过
            if (!this.getToken) {
                return;
			}

			let data = utils.queryString({
                appkey: this.appkey,
				tokenString: this.getToken,
				sids: JSON.stringify([item.sid])
			})

			let params = {
				appkey: this.appkey,
				tokenString: this.getToken
			}

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'POST',
				url: '/deleteShopcart',
				data
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				if(res.data.code === 7000) {
					this.shopcartData.splice(index,1);
					this.singleChoice();
					this.calc();
					this.$store.dispatch('getShopcartCount',params)
				}
				this.$toast(res.data.msg)
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})

			
		},
		// 修改购物车商品数量
		modifyShopcartCount(item) {
			// 判断是否登录过
            if (!this.getToken) {
                return;
            }

			let data = utils.queryString({
                appkey: this.appkey,
				tokenString: this.getToken,
				sid: item.sid,
				count: item.count
			})

			let params = {
				appkey: this.appkey,
				tokenString: this.getToken
			}
			
			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'POST',
				url: '/modifyShopcartCount',
				data
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				this.calc();
				this.$store.dispatch('getShopcartCount',params);
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})

		},
		// 单选
		singleChoice() {
			if(this.shopcartData.length === 0) {
				this.allChecked = false;
				return ;
			}
			for(let i = 0; i < this.shopcartData.length; i++) {
				if(!this.shopcartData[i].isCheck) {
					this.allChecked = false;
					break;
				}
				this.allChecked = true;
			}

			this.calc();
		},
		// 全选
		selectAll() {
			
			if(!this.allChecked) {
				this.shopcartData.forEach(item => {
					item.isCheck = false
				})
				this.calc();
				return ;
			}

			
			this.shopcartData.forEach(item => {
				item.isCheck = true
			})

			this.calc();
		},
		// 点击编辑
		onClickRight() {
			this.isEdit = !this.isEdit;
			this.isDisabled = !this.isDisabled
		},
        // 获取购物车数量
        getShopCartData() {

            // 判断是否登录过
            if (!this.getToken) {
                return;
            }

            let params = {
                appkey: this.appkey,
                tokenString: this.getToken
            }

            this.$store.dispatch('getShopcartCount', params);

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'GET',
				url: '/findAllShopcart',
				params
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				if(res.data.code === 5000) {
					res.data.result.forEach(item => {
						item.isCheck = false
					})

					this.shopcartData = res.data.result;

					console.log(this.shopcartData);
				}
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})

        },
        // 购买下单
        showMenuHandle() {
            this.$router.push({
                name: 'Menu'
            })
        }
    },
    computed: {
        ...mapGetters(['getCount','getToken'])
    }

}
</script>
